import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';

import wutil from '../../lib/wifet-util';
import LineGraph from '../../components/lineGraph';

const initialState = {};

@observer
class ParkManagement extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { wearStore } = this.props.stores;
		wearStore.generateData();

		const now = Math.floor(new Date().getTime() / 1000);
		const markerDays = wutil.timeRangeDays(-4, 11);
		this.setState({ now, markerDays });
	}

	setWindpark = (e) => {
		const { windmillStore } = this.props.stores;
		const windpark = e.target.dataset.windpark;
		windmillStore.setCurrentWindparkName(windpark);
		this.loadAll();
	}

	setWindparkStrategy = (e) => {
		const { windmillStore } = this.props.stores;
		const name = e.target.dataset.name;
		const strategy = e.target.dataset.strategy;
		windmillStore.setWindparkStrategy(name, strategy);
	}

	setWindparkLifetimeControl = (e) => {
		const { windmillStore } = this.props.stores;
		const name = e.target.dataset.name;
		const lifetimeControl = e.target.value;
		windmillStore.setWindparkLifetimeControl(name, lifetimeControl);
	}

	setWindmillLifetimeControl = (e) => {
		const { windmillStore } = this.props.stores;
		const name = e.target.dataset.name;
		const mill = e.target.dataset.mill;
		const lifetimeControl = e.target.value;
		windmillStore.setWindmillLifetimeControl(name, mill, lifetimeControl);
	}

	componentDidMount() {
		this.loadAll();
	}

	render() {
		const { now, markerDays } = this.state;
		const { windmillStore, wearStore } = this.props.stores;
		const { wears: wearData, wearsAiFiltered: aiData } = wearStore;
		const { windparks, currentWindparkName, currentWindpark } = windmillStore;

		return (
			<div class='container-fluid mt-5 pt-2' style='margin-bottom: 200px;'>
				<div class='row'>
					<div class='col-4'>
						<h1><i class='fas fa-thermometer' /> Park management</h1>
					</div>
					<div class='col-8 pt-2'>
						<ul class='nav nav-pills float-right'>
						{windparks && windparks.map(wp => (
							<li class='nav-item'>
								<button
									class={`btn btn-link nav-link ${wp.name === currentWindparkName ? 'active' : ''}`}
									onClick={this.setWindpark}
									data-windpark={wp.name}
								>{wp.name}</button>
							</li>
						))}
						</ul>
					</div>

					<div class='col-2'>
						<button type='button' class={`btn btn-${currentWindpark.strategy === 'lifetimeControl' ? '' : 'outline-'}success`}
							data-name={currentWindparkName}
							data-strategy={currentWindpark.strategy === 'lifetimeControl' ? 'default' : 'lifetimeControl'}
							onClick={this.setWindparkStrategy}
						>Lifetime control</button>
						<br />
						<div class='form-group'>
							<label for='lifetimeControl'>Years ({currentWindpark.lifetimeControl})</label>
							<input type='range' class='form-control-range' id='lifetimeControl'
								min='-10' max='10' step='1'
								data-name={currentWindparkName}
								value={currentWindpark.lifetimeControl}
								onInput={this.setWindparkLifetimeControl}
							/>
						</div>
					</div>
					<div class='col-10'>
						&nbsp;
					</div>

					<div class='col-12'>
						<table class='table table-sm table-striped'>
							<thead>
								<tr>
									<th class='w-25'>Windmill</th>
									<th class=''>Installed</th>
									<th class=''>Maintainance</th>
									<th class='text-center'>Up/down rating</th>
									<th class='text-center'>Rating (default)</th>
									<th class='text-center'>Production</th>
									<th class='text-center'>Wear</th>
									<th class='text-center'>Burnrate</th>
									<th class='text-center'>EOL</th>
									<th class='text-center'>Location</th>
								</tr>
							</thead>
							<tbody>
								{currentWindpark && currentWindpark.windmills && currentWindpark.windmills.map((mill, idx) => {
									const lifetimeControl = mill.lifetimeControl || currentWindpark.lifetimeControl;
									const lifetimeControlSec = lifetimeControl * 365 * 86400;
									let eol;
									let secLeftInLife;
									let secLeftInLifeAdjusted;
									let usedSeconds;
									let usedCapacity;
									let burnRate;

									let lifetimeSecLeftInLife;
									let lifetimeSecLeftInLifeAdjusted;
									let lifeTimeUsedCapacity;
									let lifetimeBurnRate;
									let lifetimeRating;
									let lifetimeEol;
									let showLifetimeControl = false;
									if (mill.lifetimeControl) {
										showLifetimeControl = true;
									} else if (currentWindpark.strategy === 'lifetimeControl') {
										showLifetimeControl = true;
									}

									if (mill.installed) {
										const secInLifeTime = currentWindpark.lifetime * 365 * 86400;
										const startEpoch = Math.floor(new Date(mill.installed).getTime() / 1000);
										const todayEpoch = Math.floor(new Date().getTime() / 1000);
										usedSeconds = todayEpoch - startEpoch;
										secLeftInLife = secInLifeTime - (secInLifeTime * mill.wear / 100);
										usedCapacity = usedSeconds / secInLifeTime * 100;
										burnRate = mill.wear / usedCapacity * 100;
										secLeftInLifeAdjusted = secLeftInLife * (100 / burnRate);
										eol = new Date(0);
										eol.setUTCSeconds(todayEpoch + secLeftInLifeAdjusted);

										const lifetimeSecInLifeTime = currentWindpark.lifetime * 365 * 86400 + lifetimeControlSec;
										// lifetimeSecLeftInLife = lifetimeSecInLifeTime - (secInLifeTime * mill.wear / 100);
										lifetimeSecLeftInLife = lifetimeSecInLifeTime - usedSeconds;

										lifeTimeUsedCapacity = usedSeconds / lifetimeSecInLifeTime * 100;
										lifetimeBurnRate = mill.wear / lifeTimeUsedCapacity * 100;

										lifetimeRating = Math.floor(secLeftInLife / lifetimeSecLeftInLife * 100);
										lifetimeSecLeftInLifeAdjusted = lifetimeSecLeftInLife * (100 / lifetimeBurnRate);

										lifetimeEol = new Date(0);
										// lifetimeEol.setUTCSeconds(todayEpoch + lifetimeSecLeftInLifeAdjusted);
										lifetimeEol.setUTCSeconds(todayEpoch + lifetimeSecLeftInLife);
									}

									return (
										<tr>
											<td>{mill.title}</td>
											<td>{mill.installed}</td>
											<td>{mill.maintainance}</td>
											<td class='text-center'>
												{lifetimeControl} years
												<div class='form-group'>
													<input type='range' class='form-control-range' id={`lifeTimeRange${mill.title}`}
														min='-10' max='10' step='1'
														data-name={currentWindpark.name}
														data-mill={idx}
														value={lifetimeControl}
														onInput={this.setWindmillLifetimeControl}													
													/>
												</div>
												{/* secLeftInLife: {secLeftInLife} ({util.format(secLeftInLife / 86400 / 365, 1)} years)<br /> */}
												{/* lifetimeBurnRate: {lifetimeBurnRate}<br /> */}
												{/* lifetimeSecLeftInLife: {lifetimeSecLeftInLife} ({util.format(lifetimeSecLeftInLife / 86400 / 365, 1)} years)<br /> */}
												{/* lifetimeSecLeftInLifeAdjusted: {util.format(lifetimeSecLeftInLifeAdjusted, 1)} ({util.format(lifetimeSecLeftInLifeAdjusted / 86400 / 365, 1)} years)<br /> */}
												{/* secLeftInLife / lifetimeSecLeftInLife: {util.format(secLeftInLife / lifetimeSecLeftInLife, 1)}<br /> */}
											</td>
											<td class='text-right'>
												{util.format(lifetimeRating * mill.capacity / 100, 1)} <span class='text-muted'>({mill.capacity})</span> MW
												<br />
												{showLifetimeControl && <div class='progress mt-2'>
													<div class={`progress-bar progress-bar-striped bg-${lifetimeRating > 100 ? 'danger' : 'success'}`} role='progressbar' style={`width: ${lifetimeRating}%`} aria-valuenow={lifetimeRating} aria-valuemin='0' aria-valuemax='100'>{util.format(lifetimeRating, 0)}%</div>
												</div>}
											</td>
											<td class={`text-right ${mill.production ? 'text-success' : 'text-danger'}`}>
												{mill.production ? <i class='fas fa-check mr-4' /> : <i class='fas fa-exclamation-triangle mr-4' />}
											</td>
											<td class='text-right'>
												<div class='progress'>
													<div class='progress-bar' role='progressbar' style={`width: ${mill.wear}%`} aria-valuenow={mill.wear} aria-valuemin='0' aria-valuemax='100'>{mill.wear}%</div>
												</div>
											</td>
											<td class='text-right'>
												<div class='progress'>
													<div class={`progress-bar progress-bar-striped bg-${burnRate > 100 ? 'danger' : 'success'}`} role='progressbar' style={`width: ${burnRate}%`} aria-valuenow={burnRate} aria-valuemin='0' aria-valuemax='100'>{util.format(burnRate, 0)}%</div>
												</div>
												{showLifetimeControl && <div class='progress mt-2'>
													<div class={`progress-bar progress-bar-striped bg-${lifetimeBurnRate > 100 ? 'danger' : 'success'}`} role='progressbar' style={`width: ${lifetimeBurnRate}%`} aria-valuenow={lifetimeBurnRate} aria-valuemin='0' aria-valuemax='100'>{util.format(lifetimeBurnRate, 0)}%</div>
												</div>}
											</td>
											<td class='text-right'>
												{mill.installed ? util.isoDate(eol, false, false, true) : 'n/a'}<br />
												{showLifetimeControl && <span class='text-muted'>{mill.installed ? util.isoDate(lifetimeEol, false, false, true) : 'n/a'}</span>}
											</td>
											<td class='text-right'>{mill.lat}, {mill.lng}</td>
										</tr>
									);
								})}
							</tbody>
						</table>
					</div>

				</div>
			</div>
		);
	}
}

export default ParkManagement;
